import React, { useCallback, useEffect, useMemo, useState } from "react";
import DataField from "../../../theme/components/DataField";
import Numeral from "numeral";
import { getDisplayBalance } from "../../../utils/formatBalance";
import theme from "../../../theme";
import useGetRemainingETHGain from "../../../hooks/state/stabilityPool/useGetRemainingETHGain";
import useGetTvlInARTH from "../../../hooks/state/useGetTvlInARTH";
import { useGetActiveChainId } from "../../../state/chains/hooks";
import { troveDetails } from "../../../utils/tokenDetails";

interface IProps {
  trove: string;
}

const GlobalData = (props: IProps) => {
  const {trove} = props;

  const activeChainId = useGetActiveChainId();
  const troveDetail = troveDetails(trove, activeChainId);
  const remainingETH = useGetRemainingETHGain(trove);
  const arthTvl = useGetTvlInARTH(troveDetail.contractPrefix);
  const [mahaAPR, setMahaApr] = useState<{
    data: { [key: string]: string },
    isLoading: false,
  }>({
    isLoading: false,
    data: {},
  })

  const fetchAPY = useCallback(async () => {
    const url = `https://api.arthcoin.com/apy/stability`;
    /*const headers = {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET,POST,OPTIONS',
      'Access-Control-Allow-Headers':
        'access-control-allow-headers,access-control-allow-methods,access-control-allow-origin,content-type',
    };*/

    fetch(url)
      .then((res) => res.json())
      .then((res: { [key: string]: string }) => {
        setMahaApr({ isLoading: false, data: res });
      })
      .catch((err) => {
        setMahaApr({
          isLoading: false,
          data: {},
        });
      });
  }, []);

  useEffect(() => {
    fetchAPY();
  }, [fetchAPY]);

  const displayApr = useMemo(() => {
    if (mahaAPR.isLoading) return {
      value: '-',
      isLoading: true,
    }
    if (mahaAPR.data[troveDetail.trove.toLowerCase()])
      return {
        value: Number(mahaAPR.data[troveDetail.trove.toLowerCase()]).toLocaleString('en-US', {maximumFractionDigits: 2}),
        isLoading: false,
      }
    else return {
      value: '-',
      isLoading: false,
    };
  }, [mahaAPR.data, mahaAPR.isLoading, troveDetail.trove])

  return (
    <div className={'material-primary'}>
      <DataField
        label={'TVL'}
        isValueLoading={arthTvl.isLoading}
        value={`${Numeral(getDisplayBalance(arthTvl.value, 18, 6)).format('0,0.000a')} ARTH`}
        labelFontColor={theme.color.transparent[100]}
        labelFontWeight={600}
        className={'m-b-16'}
      />
      <DataField
        label={'Remaining Rewards'}
        labelToolTip={`This amount is the ${troveDetail.depositToken.toUpperCase()} earned from ${troveDetail.depositToken.toUpperCase()} liquidation bonuses.`}
        isValueLoading={remainingETH.isLoading}
        value={Numeral(getDisplayBalance(remainingETH.value, 18, 3)).format('0,0.000a') +
          ' ' +
          troveDetail.depositToken.toUpperCase()}
        labelFontColor={theme.color.transparent[100]}
        labelFontWeight={600}
        className={'m-b-16'}
      />
      <DataField
        label={'MAHA APR'}
        isValueLoading={displayApr.isLoading}
        value={`${displayApr.value}%`}
        labelFontColor={theme.color.transparent[100]}
        // labelFontSize={16}
        // valueFontSize={16}
        labelFontWeight={600}
        valueFontColor={theme.color.primary[300]}
        valueFontWeight={600}
      />
    </div>
  )
}

export default GlobalData;
