import React, { useMemo, useState } from 'react';
import { useParams } from 'react-router-dom';

import Grid from '@material-ui/core/Grid';


import TextWrapper from '../../../theme/components/TextWrapper';

import Button from "../../../theme/components/Button";
import useClaimCollateral from '../../../hooks/callbacks/borrowerOperations/useClaimCollateral';
import useUnclaimedCollateral from '../../../hooks/state/borrowerOperations/useUnclaimedCollateral';

export default (props: { contractPrefix: string }) => {
  const action = useClaimCollateral(props.contractPrefix)

  const claimableCollateral = useUnclaimedCollateral(props.contractPrefix)

  if (claimableCollateral.value.eq(0)) return <div />

  return (
    <div className={'material-secondary'}>
      <Grid container spacing={2} justify={'space-between'} alignItems={'center'}>
        <Grid item lg={6} md={6} sm={12} xs={12}>
          <TextWrapper
            text={'You have unclaimed collateral from your loan that ' +
              'got closed because it was fully redeemed. Click on the button to claim your collateral back.'}
          />
        </Grid>
        <Grid item lg={3} md={3} sm={6} xs={6}>
          <Button
            onClick={action}
            text={'Claim Collateral'}
          />
        </Grid>
      </Grid>
    </div>

  );
}
