import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import { LOADING_DEFAULT_BASIC_STATE } from '../../utils/constants';
import { formatToBN } from '../../utils/formatBalance';
import { BasicState } from '../../utils/interface';
import { coingeckoTokenMapping } from '../../utils/tokens/constants';

const useGetUSDValue = (token: string) => {
  const [state, setState] = useState<BasicState>(LOADING_DEFAULT_BASIC_STATE);

  const { account } = useWallet();

  const fetchValue = useCallback(async () => {
      const id = coingeckoTokenMapping[token];
      const usdValue = await fetch(
        `https://api.coingecko.com/api/v3/simple/price?ids=${id}&vs_currencies=usd`
      )
        .then((data) => data.json())
        .then((data: any) => {
          const value = Number(data[id].usd);
          if (value) {
            return value;
          } else {
            return 0;
          }
        })
        .catch((error) => {
          console.error(`Failed to ${token} usd value: ${error.stack}`);
          return 0;
        });
      setState({
        isLoading: false,
        value: formatToBN(usdValue)
      })
  }, [token]);

  useEffect(() => {
    fetchValue().catch((err) => {
      console.error(`Failed to tvl in usd: ${err.stack}`);
    });
  }, [account, fetchValue]);

  return state;
};

export default useGetUSDValue;
