import { Grid } from "@material-ui/core";
import SummaryBox from "./SummaryBox";
import Numeral from "numeral";
import { formatToBN, getDisplayBalance } from "../../../utils/formatBalance";
import React from "react";
import useGetProtocolDebt from "../../../hooks/state/useGetProtocolDebt";
import useGetProtocolCollateral from "../../../hooks/state/useGetProtocolCollateral";
import configurations from "../../../config";
import { useGetActiveChainId } from "../../../state/chains/hooks";
import useCollateralPriceFeed from "../../../hooks/state/useCollateralPriceFeed";
import useGetTotalCollateralRatio from "../../../hooks/state/troveManager/useGetTotalCollateralRatio";
import useGetUSDValue from "../../../hooks/state/useGetUSDValue";

const GlobalData = () => {
  const entireSystemDebt = useGetProtocolDebt();
  const chainId = useGetActiveChainId();
  const price = useCollateralPriceFeed(configurations[chainId].defaultCollateral, configurations[chainId].defaultCollateral);

  const entireSystemCollateral = useGetProtocolCollateral();
  const tcr = useGetTotalCollateralRatio(configurations[chainId].defaultCollateral);
  const chainTokenUSD = useGetUSDValue(configurations[chainId].defaultCollateral);
  const arth = {
    isLoading: false,
    value: formatToBN(2),
  };


  return (
    <div className="mo-custom-container m-b-40">
      <Grid container spacing={2}>
        <Grid item lg={3} md={6} sm={12} xs={12}>
          <SummaryBox
            text={'Total Collateral Ratio'}
            isLoading={tcr.isLoading}
            value={Numeral(getDisplayBalance(tcr.value, 18, 6)).format('0,0.000%')}
          />
        </Grid>
        <Grid item lg={3} md={6} sm={12} xs={12}>
          <SummaryBox
            text={'Total Collateral Locked'}
            isLoading={entireSystemCollateral.isLoading}
            value={Numeral(getDisplayBalance(entireSystemCollateral.value, 18, 3)).format('0,0') + ' ' + configurations[chainId].defaultCollateral}
            subtitle={`$${Number(getDisplayBalance(entireSystemCollateral.value.mul(chainTokenUSD.value), 36, 6))
              .toLocaleString('en-US', {maximumFractionDigits: 2})}`}
          />
        </Grid>
        <Grid item lg={3} md={6} sm={12} xs={12}>
          <SummaryBox
            text={'Protocol Price'}
            isLoading={price.isLoading}
            value={Numeral(getDisplayBalance(price.value, 18, 3)).format('0,0.000') + `ARTH/${configurations[chainId].defaultCollateral}`}
            subtitle={`${Number(getDisplayBalance(price.value.mul(arth.value), 36, 6))
              .toLocaleString('en-US', {maximumFractionDigits: 2})} USD/${configurations[chainId].defaultCollateral}`}
          />
        </Grid>
        <Grid item lg={3} md={6} sm={12} xs={12}>
          <SummaryBox
            text={'Total Debt'}
            isLoading={entireSystemDebt.isLoading}
            value={Numeral(getDisplayBalance(entireSystemDebt.value, 18, 3)).format('0,0') + ' ARTH'}
            subtitle={`$${Number(getDisplayBalance(entireSystemDebt.value.mul(arth.value), 36, 6))
              .toLocaleString('en-US', {maximumFractionDigits: 2})}`}
          />
        </Grid>
      </Grid>
    </div>
  )
}

export default GlobalData;
