import React from "react";
import Loader from "react-spinners/BeatLoader";

import TextWrapper from "../../../theme/components/TextWrapper";
import theme from "../../../theme";

interface Iprops {
  text: string;
  value: string;
  isLoading: boolean;
  subtitle?: string;
}

const SummaryBox = (props: Iprops) => {
  const { text, value, isLoading, subtitle } = props;

  return (
    <div className="material-primary text-center">
      <div className="m-b-4">
        <TextWrapper text={text} fontSize={16} align={"center"} />
      </div>
      <div className={'m-b-2'}>
        {isLoading ? (
          <Loader color={"#ffffff"} loading={true} size={8} margin={2} />
        ) : (
          <TextWrapper text={value} fontSize={18} fontWeight={"bold"} align={"center"} />
        )}
      </div>
      {subtitle && <TextWrapper text={subtitle} fontSize={14} align={"center"} Fcolor={theme.color.transparent[100]}/>}
    </div>
  );
};

export default SummaryBox;
