import React from "react";
import IconLoader from "../../../theme/components/IconLoader";
import {makeStyles,} from "@material-ui/core/styles";
import Button from "../../../theme/components/Button";
import TextWrapper from "../../../theme/components/TextWrapper";
import {DataGrid, GridColDef} from "@material-ui/data-grid";
import '../../../customCss/Custom-Mahadao-Data-Table.css';
import TextButton from "../../../theme/components/TextButton";
import {IV3GaugeStaticData } from "../../../protocol/config";
import useGetV3StakingAPR from "../../../hooks/state/farming/useGetV3StakingAPR";
import Loader from "react-spinners/BeatLoader";
import {useGetActiveChainId} from "../../../state/chains/hooks";
import useCore from "../../../hooks/useCore";

export function SortedDescendingIcon() {
  return <IconLoader iconName={'ArrowTailUp'} iconType="arrow"/>;
}

export function SortedAscendingIcon() {
  return <IconLoader iconName={'ArrowTailDown'} iconType="arrow"/>;
}

const useStyles = makeStyles({
  root: {
    '& .super-app-theme--header': {
      cursor: 'pointer',
    },
    '& .super-app.negative': {
      color: '#FA4C69',
      fontWeight: '600',
    },
    '& .super-app.positive': {
      color: '#20C974',
      fontWeight: '600',
    },
    '& .super-app.loss': {
      color: '#FA4C69',
      fontWeight: '600',
    },
    '& .super-app.profit': {
      color: '#20C974',
      fontWeight: '600',
    },
  },
  gridStyle: {
    color: 'white',
  },
});

interface IProps {
  data: IV3GaugeStaticData[];
}

const DataTable = (props: IProps) => {
  const classes = useStyles();
  const apr = useGetV3StakingAPR();
  const chainId = useGetActiveChainId();
  const core = useCore();

  const columns: GridColDef[] = [
    {
      field: 'poolName',
      headerName: 'Pool Name',
      flex: 0.5,
      sortable: false,
      renderCell: (params: any) => {
        return (
          <div className={'start-center'}>
            <span>{params.value}</span>
            <div className={'single-line-center-start'}>
              <TextButton
                onClick={() => window.open(`${params.row.liquidityLink}`)}
                text={`Add Liquidity`}
                fontSize={12}
              />
              <IconLoader iconName={'ArrowLinkColored'} iconType={'arrow'} width={16} height={16}/>
            </div>

          </div>
        );
      },
    },
    /*{
      field: 'poolFee',
      headerName: 'Pool Fee',
      flex: 0.2,
      sortable: false,
      renderCell: (params: any) => {
        return (
          <div className={'start-center'}>{params.value}%</div>
        )
      },
    },*/
    {
      field: 'apr',
      headerName: 'APR',
      flex: 0.3,
      sortable: false,
      renderCell: (params: any) => {
        const address = core._contracts[chainId][params.row.gauge].address;
        const calc = apr.data[address];
        return (
          <div className={'start-center'}>
            {
              apr.isLoading
                ? <Loader color={'#ffffff'} loading={true} size={2} margin={2}/>
                : calc ? Number(calc)
                    .toLocaleString('en-Us', {maximumFractionDigits: 2}) + '%' + ' - ' +
                  (Number(calc) * 5)
                    .toLocaleString('en-Us', {maximumFractionDigits: 2}) + '%'

                  : '-'
            }
          </div>
        )
      },
    },
    {
      field: 'action',
      headerName: 'Action',
      flex: 0.2,
      sortable: false,
      renderCell: (params: any) => {
        return (
          <div style={{marginTop: 'auto', marginBottom: 'auto', width: '150px'}}>
            <Button
              text={'Visit pool'}
              href={`https://gov.mahadao.com/#/v3Staking/${params.row.pool}`}
              tracking_id={'visit_v3Staking_pool'}
              tracking_params={{
                collateral: `${params.row.poolName}`
              }}
            />
          </div>
        )
      },
    },
  ];

  const noResultsComponent = () => {
    return (
      <div className="noResultContainer">
        <div className="internal">
          <TextWrapper
            text={'No v3 gauges found'}
            fontSize={24}
            fontWeight={'bold'}
            className="m-b-8"
            align="center"
          />
        </div>
      </div>
    );
  };

  return (
    <div className={classes.root} style={{position: 'relative'}}>
      <DataGrid
        className={classes.gridStyle}
        getRowId={(rows) => rows.pool}
        rows={props.data}
        columns={columns}
        pageSize={10}
        hideFooterPagination={false}
        rowHeight={80}
        autoHeight={true}
        disableColumnMenu={true}
        loading={false}
        components={{
          NoRowsOverlay: noResultsComponent,
          ColumnSortedDescendingIcon: SortedDescendingIcon,
          ColumnSortedAscendingIcon: SortedAscendingIcon,
        }}
        disableColumnSelector={true}
        disableSelectionOnClick={true}
      />
    </div>
  )
}

export default DataTable;
