import { BigNumber } from 'ethers';
import { useCallback, useEffect, useState } from 'react';
import { useWallet } from 'use-wallet';

import useCore from '../useCore';

import useGetEntireSystemCollateral from './troveManager/useGetEntireSystemCollateral';
import useCollateralPriceFeed from './useCollateralPriceFeed';
import { useGetActiveChainId } from "../../state/chains/hooks";
import useTokenDecimals from "../useTokenDecimals";

type State = {
  isLoading: boolean;
  value: BigNumber;
  gmuValue: BigNumber;
};

export default (token: string, contractPrefix: string) => {
  const [value, setValue] = useState<State>({
    isLoading: true,
    value: BigNumber.from(0),
    gmuValue: BigNumber.from(0),
  });

  const core = useCore();
  const chainId = useGetActiveChainId();
  const { account } = useWallet();
  const collateralGMUPrice = useCollateralPriceFeed(token, contractPrefix);
  const entireSystemCollateral = useGetEntireSystemCollateral(contractPrefix);
  const tokenDecimals = useTokenDecimals(token);

  const fetchValue = useCallback(async () => {
    if (!account) {
      setValue({ isLoading: false, value: BigNumber.from(0), gmuValue: BigNumber.from(0) });
      return;
    }

    if (collateralGMUPrice.isLoading || entireSystemCollateral.isLoading) {
      return;
    }

    const gmuValue = entireSystemCollateral.value
      .mul(collateralGMUPrice.value)
      .div(BigNumber.from(10).pow(18))
    // + (18 - (18 - tokenDecimals))))


    setValue({ isLoading: false, value: entireSystemCollateral.value, gmuValue: gmuValue });
  }, [account, collateralGMUPrice.isLoading, collateralGMUPrice.value, entireSystemCollateral.isLoading, entireSystemCollateral.value, tokenDecimals]);

  useEffect(() => {
    if (core.isUnlocked) {
      fetchValue().catch((err) =>
        console.error(`Failed to fetch arth in circulating supply with %: ${err}`),
      );
    }
  }, [core.isUnlocked, account, fetchValue]);

  return value;
};
