import CustomInputContainer from "../../../components/CustomInputContainer";
import { getDisplayBalance } from "../../../utils/formatBalance";
import { BigNumber } from "ethers";
import TextWrapper from "../../../theme/components/TextWrapper";
import Loader from "react-spinners/BeatLoader";
import theme from "../../../theme";
import Grid from "@material-ui/core/Grid";
import Button from "../../../theme/components/Button";
import React, { useMemo, useState } from "react";
import useCore from "../../../hooks/useCore";
import { useGetActiveChainId } from "../../../state/chains/hooks";
import useTokenBalance from "../../../hooks/state/useTokenBalance";
import { troveDetails } from "../../../utils/tokenDetails";
import useApprove, { ApprovalState } from "../../../hooks/callbacks/useApprove";
import useStake from "../../../hooks/callbacks/stabilityPool/useStake";
import { parseUnits } from "ethers/lib/utils";
import { ValidateNumber } from "../../../components/CustomInputContainer/RegexValidation";
import InfoTip from "../../../theme/components/InfoTip";
import useETHGain from "../../../hooks/state/stabilityPool/useETHGain";
import useGetMAHARewards from "../../../hooks/state/stabilityPool/useGetMAHARewards";

interface IProps {
  trove: string;
}

const Deposit = (props: IProps) => {
  const { trove } = props;
  const [arthValue, setARTHValue] = useState<string>('0');
  const [isInputFieldError, setIsInputFieldError] = useState<boolean>(false);

  const core = useCore();
  const chainId = useGetActiveChainId();
  const ethGain = useETHGain(trove);
  const mahaGain = useGetMAHARewards(trove);
  const arthBalance = useTokenBalance(core.tokens('ARTH', chainId));
  const troveDetail = troveDetails(trove, chainId);

  const stake = useStake(troveDetail.contractPrefix, BigNumber.from(parseUnits(arthValue, 18)));

  const handleClose = () => {
    setARTHValue('0');
  };

  const handleStake = () => {
    stake(() => {
      setARTHValue('0');
      handleClose();
    });
  };

  const onValueChange = async (val: string) => {
    if (val === '') {
      setARTHValue('0');
      return;
    }

    const check: boolean = ValidateNumber(val);
    if (!check) return;
    setARTHValue(check ? val : String(Number(val)));
  };


  return (
    <div>
      <CustomInputContainer
        isBalanceLoading={arthBalance.isLoading}
        IBalanceValue={getDisplayBalance(BigNumber.from(arthBalance.value), 18)}
        ILabelValue={'How much would you like to deposit?'}
        showBalance={true}
        DefaultValue={arthValue}
        LogoSymbol={'ARTH'}
        SymbolText={'ARTH'}
        dontShowBackgroundContainer={true}
        setText={(t) => onValueChange(t)}
        inputMode={'decimal'}
        tagText={'MAX'}
        hasDropDown={false}
        errorCallback={(flag: boolean) => {
          setIsInputFieldError(flag);
        }}
      />
      <div className={'m-t-32'}>
        {(ethGain.value.gt(0) || mahaGain.value.gt(0)) && (
          <div>
            <div className="single-line-start-between m-b-8">
              <div className="single-line-center-start">
                <TextWrapper
                  text={'Gain you will recieve'}
                  fontSize={12}
                />
              </div>
              <div>
                <div className="single-line-center-end m-b-2">
                  {(ethGain.isLoading) ? (
                    <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
                  ) : (
                    <TextWrapper
                      text={
                        Number(getDisplayBalance(ethGain.value, 18, 3)).toLocaleString() +
                        ' ' +
                        troveDetail.depositToken.toUpperCase()
                      }
                      fontSize={12}
                      Fcolor={theme.color.green[300]}
                    />
                  )}
                </div>
                <div className="single-line-center-end">
                  {(mahaGain.isLoading) ? (
                    <Loader color={'#ffffff'} loading={true} size={8} margin={2} />
                  ) : (
                    <TextWrapper
                      text={
                        `${Number(getDisplayBalance(mahaGain.value, 18, 3)).toLocaleString()} MAHA`
                      }
                      fontSize={12}
                      Fcolor={theme.color.green[300]}
                    />
                  )}
                </div>
              </div>
            </div>
            <InfoTip type={'Info'} msg={'While depositing again you will receive your rewards too'} className={'m-b-8'}/>
          </div>
        )}
        <Grid container spacing={2}>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                variant={'transparent'}
                onClick={() => handleClose()}
                tracking_id={(ethGain.value.gt(0) || mahaGain.value.gt(0)) ? `deposit&claim_S_P` : `deposit_S_P`}
                tracking_params={{
                  collateral: troveDetail.trove,
                  action: 'cancel',
                  amount: Number(arthValue),
                }}
              >
                Cancel
              </Button>
            </Grid>
            <Grid item lg={6} md={6} sm={12} xs={12}>
              <Button
                disabled={isInputFieldError || !Number(arthValue)}
                onClick={handleStake}
                tracking_id={(ethGain.value.gt(0) || mahaGain.value.gt(0)) ? `deposit&claim_S_P` : `deposit_S_P`}
                tracking_params={{
                  collateral: troveDetail.trove,
                  action: 'confirm',
                  amount: Number(arthValue),
                }}
              >
                {(ethGain.value.gt(0) || mahaGain.value.gt(0)) ? 'Deposit and Claim' : 'Deposit'}
              </Button>
            </Grid>
          </Grid>
      </div>
    </div>
  )
}

export default Deposit;
