import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';

import { useGetActiveChainId } from './state/chains/hooks';
import NoPageFound from './theme/components/NoPageFound';
import Page from './theme/components/Page/Page';
import AddLoan from './views/AddLoan';
import Farming from './views/Farming';
import Faucet from './views/Faucet';
import Loan from './views/Loan';
import Pools from './views/Pools';
import Redeem from './views/Redeem';
import Tokenization from './views/Tokenization';
import LoanDetails from "./views/LoanDetails";
import ModifyLoan from "./views/ModifyLoan";
import AddLiquidity from "./views/AddLiquidity";
import Migration from "./views/Migration";
import Trade from "./views/Trade";
import Price from "./views/Price";
import ComingSoon from "./components/ComingSoon";
import StabilityPool from "./views/StabilityPool";
import Depreciated from "./components/Depreciated";
import RedeemARTH from "./views/RedeemARTH";
import config from "./config";
import UniswapV3Gauges from "./views/UniswapV3Gauges";

const Navigation = () => {
  const activeChainId = useGetActiveChainId();
  let defaultUrl = '/loans/all-loans'

  if (activeChainId === 1) {
    defaultUrl = '/loans/all-loans'
  } else if (activeChainId === 137) {
    defaultUrl = '/redeem'
  }

  return (
    <Switch>
      <Route exact path="/">
        <Redirect to={defaultUrl} />
      </Route>
      <Route path="/price">
        <Page>
          <Price />
        </Page>
      </Route>
      <Route path="/loans/:mode">
        <Page>
          {activeChainId !== 137
            ? <Loan />
            : <Depreciated />
          }
        </Page>
      </Route>
      <Route path="/loan/add/:id">
        <Page>
          {activeChainId === 1 || activeChainId === 97
            ? <AddLoan />
            : <Depreciated />
          }
        </Page>
      </Route>
      <Route path="/loan/details/:ownerAddress/:trove">
        <Page>
          <LoanDetails />
        </Page>
      </Route>
      <Route path="/loan/modify/:trove">
        <Page>
          <ModifyLoan />
        </Page>
      </Route>
      <Route path="/redeem/:id">
        {
          activeChainId === 137
            ? <Redirect to={'/redeem'} />
            : <Page>
              <Redeem />
            </Page>
        }
      </Route>
      <Route path="/stability-pool/:id">
        <Page>
          {activeChainId !== 137
            ? <StabilityPool />
            : <Depreciated />
          }
        </Page>
      </Route>
      {activeChainId !== 1 && <Route path="/tokenization">
        <Page>
          <Tokenization />
        </Page>
      </Route>}
      <Route path="/migration">
        <Page>
          <Migration />
        </Page>
      </Route>
      <Route path="/redeem">
        <Page>
          {activeChainId === 137
            ? <RedeemARTH />
            : <Redirect to={`/redeem/${config[activeChainId].defaultCollateral}`} />
          }
        </Page>
      </Route>
      {/* <Route path="/pools/add-liquidity/:poolName">
        <Page>
          {(activeChainId === 137 || activeChainId === 56)
            ? <AddLiquidity />
            : <ComingSoon />
          }
        </Page>
      </Route>*/}
      {/*<Route path="/pools">
        <Page>
          {(activeChainId === 137 || activeChainId === 56)
            ? <Pools />
            : <ComingSoon />
          }
        </Page>
      </Route>
      <Route path="/trade">
        <Page>
          {(activeChainId === 137 || activeChainId === 56)
            ? <Trade />
            : <ComingSoon />
          }
        </Page>
      </Route>*/}
      <Route path="/faucet">
        {activeChainId === 97 || activeChainId === 80001 ? (
          <Page>
            <Faucet />
          </Page>
        ) : (
          <Redirect to={defaultUrl} />
        )}
      </Route>
      <Route path="/farming">
        <Page>
          <UniswapV3Gauges />
        </Page>
      </Route>
      <Route exact path="*">
        <NoPageFound />
      </Route>
    </Switch>
  );
};

export default Navigation;
